import Label from "components/Label";
import Text from "components/Text/Text";
import BaseButton, { IBaseButtonProps } from "components/base/BaseButton/BaseButton";
import { IButtonCommonStyleProps } from "components/interface/Button";
import { SelectButton, SelectButtonProps } from "primereact/selectbutton";
import React from "react";
import { useTranslation } from "react-i18next";
import blueTick from "assets/img/buttonIcons/blue-tick.svg";
interface ISimpleChipProps extends IBaseButtonProps, IButtonCommonStyleProps {
  isSelected?: any;
  classNames?: string;
}

const SimpleChipWithoutMemo: React.FC<ISimpleChipProps> = (props: ISimpleChipProps) => {
  const { isSelected, classNames, ...primeReactProps } = props;
  const { disabled, label, onClick } = primeReactProps;

  const { t } = useTranslation();
  const translatedLabel = t(label ?? "");

  // const defaultItemTemplate=(options:any)=>{
  //   const isSelected =options.value ===value
  //   console.log("isSelected", isSelected);

  //   return  <div className={`${isSelected ?" bg-white text-primary-sapphireBlue-600 !rounded-md ":""} py-2 px-9 text-xs flex-shrink-0 `}>{options.name}</div> ;
  // }
  const getClassName = () => {
    return `${
      isSelected
        ? "border border-primary-sapphireBlue-600 bg-primary-sapphireBlue-200 text-primary-sapphireBlue-800 "
        : " bg-white border border-primary-gray-400 text-primary-gray-900"
    } text-xs rounded-chip w-fit h-9 px-14 ${classNames ?? ""} `;
  };
  return (
    <div className="relative ">
      <BaseButton className={getClassName()} label={translatedLabel} onClick={onClick} />
      {isSelected ? (
        <div className="w-6 h-6 absolute top-1.5 left-1.5 flex justify-center items-center">
          <img src={blueTick} alt="" />
        </div>
      ) : null}
    </div>
  );
};

// Memoization

const SimpleChip = React.memo(SimpleChipWithoutMemo);
// redux wiring

// exports
export default SimpleChip;
